$bg-gray: #555;
$white: #ddd;

*, *::after, *::before{
    margin: 0;
    padding: 0;
}

body{
    margin: 0;
    padding: 0;
    background-color: $bg-gray;
    color: $white;
    font-family: 'Lobster', cursive;
}

.container{
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    overflow: hidden;
}

.container div h1{
    cursor: default;
    user-select: none;
}

.up-left{
    transform: translate(-3000px, -1440px);
    animation: up-left 2s both;
}

.down-right{
    transform: translate(3000px, 1440px);
    animation: up-left 2s both;
}

@keyframes up-left{
    to{
        transform: translate(0,0);
    }
}

@keyframes down-right{
    to{
        transform: translate(0,0);
    }
}
